/* src/components/AboutUs.css */

.about-container {
  background-color: #e0f2e9; /* Light green background */
  min-height: 100vh;        /* Ensure it covers the full height of the viewport */
  padding: 20px;            /* Add some padding */
  box-sizing: border-box;   /* Include padding in the total height and width */
}

.founder-details {
  text-align: center;
  margin-bottom: 40px;      /* Space between sections */
}

.founder-image {
  width: 180px;             /* Increased width of the profile picture */
  height: 250px;            /* Increased height for a longer profile pic */
  border-radius: 20px;      /* Adjust this value for more or less curvature on corners */
  object-fit: cover;        /* Ensure the image covers the area */
  margin-bottom: 20px;      /* Space below the image */
}

.mission-vision,
.community-engagement {
  margin: 20px 0;           /* Space above and below each section */
  text-align: left;         /* Align text to the left */
}
