/* src/components/Gallery.css */

.gallery-container {
  background-color: white; /* Set background color to white */
  padding: 20px;          /* Add padding around the gallery */
  display: grid;          /* Use CSS Grid for layout */
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); /* Responsive grid */
  gap: 10px;              /* Space between grid items */
}

.gallery-item {
  border: 2px solid #004d40; /* Optional: Add border to gallery items */
  border-radius: 10px;       /* Optional: Add rounded corners */
  overflow: hidden;          /* Ensure rounded corners are applied to images */
}

.gallery-item img {
  width: 100%;              /* Make the images responsive */
  height: auto;            /* Maintain aspect ratio */
  display: block;          /* Remove space below images */
}
